import toast from 'react-hot-toast'
import { AxiosError } from 'axios'

export enum ErrorType {
  NETWORK = 'network',
  AUTH = 'auth', 
  VALIDATION = 'validation',
  SERVER = 'server',
  WEBSOCKET = 'websocket',
  API = 'api',
  UNKNOWN = 'unknown'
}

export interface AppError {
  type: ErrorType
  code?: string | number
  message: string
  details?: any
  timestamp: number
  source?: string
}

export class ErrorHandler {
  private static instance: ErrorHandler
  private errorListeners: Array<(error: AppError) => void> = []

  static getInstance(): ErrorHandler {
    if (!ErrorHandler.instance) {
      ErrorHandler.instance = new ErrorHandler()
    }
    return ErrorHandler.instance
  }

  /**
   * 
   */
  handle(error: any, source?: string, showToast: boolean = true): AppError {
    const appError = this.parseError(error, source)
    
    // 
    this.logError(appError)
    
    // 
    if (showToast) {
      this.showErrorToast(appError)
    }
    
    // 
    this.notifyListeners(appError)
    
    return appError
  }

  /**
   * 
   */
  private parseError(error: any, source?: string): AppError {
    const timestamp = Date.now()
    
    // Axios HTTP
    if (error?.isAxiosError || error?.response) {
      return this.parseAxiosError(error, source, timestamp)
    }
    
    // WebSocket
    if (error?.type === 'websocket' || source === 'websocket') {
      return {
        type: ErrorType.WEBSOCKET,
        message: this.getWebSocketErrorMessage(error),
        details: error,
        timestamp,
        source
      }
    }
    
    // JavaScript
    if (error instanceof Error) {
      return {
        type: ErrorType.UNKNOWN,
        message: error.message,
        details: {
          name: error.name,
          stack: error.stack
        },
        timestamp,
        source
      }
    }
    
    // 
    if (typeof error === 'string') {
      return {
        type: ErrorType.UNKNOWN,
        message: error,
        timestamp,
        source
      }
    }
    
    // 
    return {
      type: ErrorType.UNKNOWN,
      message: '',
      details: error,
      timestamp,
      source
    }
  }

  /**
   * Axios
   */
  private parseAxiosError(error: AxiosError, source?: string, timestamp?: number): AppError {
    const { response, request, message } = error

    if (response) {
      // 
      const status = response.status
      const data = response.data as any

      if (status === 401) {
        return {
          type: ErrorType.AUTH,
          code: status,
          message: data?.message || '',
          details: data,
          timestamp: timestamp || Date.now(),
          source
        }
      }

      if (status === 403) {
        return {
          type: ErrorType.AUTH,
          code: status,
          message: data?.message || '',
          details: data,
          timestamp: timestamp || Date.now(),
          source
        }
      }

      if (status === 422 || status === 400) {
        return {
          type: ErrorType.VALIDATION,
          code: status,
          message: data?.message || '',
          details: data,
          timestamp: timestamp || Date.now(),
          source
        }
      }

      if (status === 429) {
        return {
          type: ErrorType.API,
          code: status,
          message: data?.message || '',
          details: data,
          timestamp: timestamp || Date.now(),
          source
        }
      }

      if (status >= 500) {
        return {
          type: ErrorType.SERVER,
          code: status,
          message: data?.message || '',
          details: data,
          timestamp: timestamp || Date.now(),
          source
        }
      }

      return {
        type: ErrorType.API,
        code: status,
        message: data?.message || ` (${status})`,
        details: data,
        timestamp: timestamp || Date.now(),
        source
      }
    }

    if (request) {
      // 
      return {
        type: ErrorType.NETWORK,
        message: '',
        details: { message, request },
        timestamp: timestamp || Date.now(),
        source
      }
    }

    // 
    return {
      type: ErrorType.UNKNOWN,
      message: message || '',
      details: error,
      timestamp: timestamp || Date.now(),
      source
    }
  }

  /**
   * WebSocket
   */
  private getWebSocketErrorMessage(error: any): string {
    if (error?.code) {
      switch (error.code) {
        case 1006:
          return '...'
        case 1000:
          return ''
        case 1001:
          return ''
        case 1002:
          return ''
        case 1003:
          return ''
        case 1005:
          return ''
        case 1011:
          return ''
        default:
          return `WebSocket (${error.code})`
      }
    }
    
    return error?.message || 'WebSocket'
  }

  /**
   * 
   */
  private showErrorToast(error: AppError): void {
    const { type, message } = error

    switch (type) {
      case ErrorType.AUTH:
        toast.error(message, { 
          id: 'auth-error',
          duration: 5000,
          icon: ''
        })
        break
        
      case ErrorType.NETWORK:
        toast.error(message, {
          id: 'network-error',
          duration: 6000,
          icon: ''
        })
        break
        
      case ErrorType.VALIDATION:
        toast.error(message, {
          id: 'validation-error',
          duration: 4000,
          icon: ''
        })
        break
        
      case ErrorType.WEBSOCKET:
        toast.error(message, {
          id: 'websocket-error',
          duration: 3000,
          icon: ''
        })
        break
        
      case ErrorType.SERVER:
        toast.error(message, {
          id: 'server-error',
          duration: 5000,
          icon: ''
        })
        break
        
      default:
        toast.error(message, {
          duration: 4000,
          icon: ''
        })
    }
  }

  /**
   * 
   */
  private logError(error: AppError): void {
    const logLevel = this.getLogLevel(error.type)
    const logMessage = `[${error.type.toUpperCase()}] ${error.message}`
    
    if (import.meta.env.MODE === 'development') {
      // 
      console.group(` Error: ${logMessage}`)
      console.error('Details:', error)
      console.error('Timestamp:', new Date(error.timestamp).toISOString())
      if (error.source) console.error('Source:', error.source)
      console.groupEnd()
    } else {
      // 
      console.error(logMessage, {
        type: error.type,
        code: error.code,
        source: error.source,
        timestamp: error.timestamp
      })
    }

    // TODO: 
    // this.sendToLoggingService(error)
  }

  /**
   * 
   */
  private getLogLevel(errorType: ErrorType): 'error' | 'warn' | 'info' {
    switch (errorType) {
      case ErrorType.AUTH:
      case ErrorType.SERVER:
        return 'error'
      case ErrorType.NETWORK:
      case ErrorType.WEBSOCKET:
        return 'warn'
      default:
        return 'info'
    }
  }

  /**
   * 
   */
  addErrorListener(listener: (error: AppError) => void): void {
    this.errorListeners.push(listener)
  }

  /**
   * 
   */
  removeErrorListener(listener: (error: AppError) => void): void {
    this.errorListeners = this.errorListeners.filter(l => l !== listener)
  }

  /**
   * 
   */
  private notifyListeners(error: AppError): void {
    this.errorListeners.forEach(listener => {
      try {
        listener(error)
      } catch (err) {
        console.error('Error in error listener:', err)
      }
    })
  }

  /**
   * 
   */
  createRetryableHandler(
    maxRetries: number = 3,
    retryDelay: number = 1000
  ) {
    return async (
      operation: () => Promise<any>,
      context?: string
    ): Promise<any> => {
      let lastError: any
      
      for (let attempt = 1; attempt <= maxRetries; attempt++) {
        try {
          return await operation()
        } catch (error) {
          lastError = error
          const appError = this.parseError(error, context)
          
          // 
          if (appError.type === ErrorType.AUTH || 
              appError.type === ErrorType.VALIDATION) {
            throw error
          }
          
          if (attempt === maxRetries) {
            this.handle(error, context)
            throw error
          }
          
          // 
          await new Promise(resolve => 
            setTimeout(resolve, retryDelay * Math.pow(2, attempt - 1))
          )
        }
      }
      
      throw lastError
    }
  }
}

// 
export const errorHandler = ErrorHandler.getInstance()

// 
export const handleError = (error: any, source?: string, showToast: boolean = true): AppError => {
  return errorHandler.handle(error, source, showToast)
}

// 
export const createRetryHandler = (maxRetries?: number, retryDelay?: number) => {
  return errorHandler.createRetryableHandler(maxRetries, retryDelay)
}