/**
 * 
 * @param value 
 * @param currency 
 * @param decimals 2
 * @returns 
 */
export const formatCurrency = (
  value: number,
  currency = '',
  decimals = 2
): string => {
  const formatted = new Intl.NumberFormat('zh-CN', {
    minimumFractionDigits: decimals,
    maximumFractionDigits: decimals
  }).format(value)
  
  return currency ? `${currency}${formatted}` : formatted
}

/**
 * 
 * @param value 0-1
 * @param decimals 1
 * @param isAlreadyPercent 12.512.5%
 * @returns 
 */
export const formatPercent = (
  value: number,
  decimals = 1,
  isAlreadyPercent = false
): string => {
  const percent = isAlreadyPercent ? value : value * 100
  const formatted = percent.toFixed(decimals)
  return `${formatted}%`
}

/**
 * KMB
 * @param value 
 * @param decimals 1
 * @returns 
 */
export const formatLargeNumber = (
  value: number,
  decimals = 1
): string => {
  const abs = Math.abs(value)
  const sign = value < 0 ? '-' : ''
  
  if (abs >= 1e9) {
    return `${sign}${(abs / 1e9).toFixed(decimals)}B`
  }
  if (abs >= 1e6) {
    return `${sign}${(abs / 1e6).toFixed(decimals)}M`
  }
  if (abs >= 1e3) {
    return `${sign}${(abs / 1e3).toFixed(decimals)}K`
  }
  
  return value.toString()
}

/**
 * 21
 * @param date 
 * @returns 
 */
export const formatRelativeTime = (date: Date | number): string => {
  const now = new Date()
  const past = new Date(date)
  const diffInSeconds = Math.floor((now.getTime() - past.getTime()) / 1000)
  
  if (diffInSeconds < 60) {
    return ''
  }
  
  const diffInMinutes = Math.floor(diffInSeconds / 60)
  if (diffInMinutes < 60) {
    return `${diffInMinutes}`
  }
  
  const diffInHours = Math.floor(diffInMinutes / 60)
  if (diffInHours < 24) {
    return `${diffInHours}`
  }
  
  const diffInDays = Math.floor(diffInHours / 24)
  if (diffInDays < 7) {
    return `${diffInDays}`
  }
  
  // 7
  return past.toLocaleDateString('zh-CN')
}

/**
 * 
 * @param date 
 * @param format 
 * @returns 
 */
export const formatDateTime = (
  date: Date | number | string,
  format: 'date' | 'time' | 'datetime' | 'short' = 'datetime'
): string => {
  const d = new Date(date)
  
  if (isNaN(d.getTime())) {
    return ''
  }
  
  const year = d.getFullYear()
  const month = String(d.getMonth() + 1).padStart(2, '0')
  const day = String(d.getDate()).padStart(2, '0')
  const hours = String(d.getHours()).padStart(2, '0')
  const minutes = String(d.getMinutes()).padStart(2, '0')
  const seconds = String(d.getSeconds()).padStart(2, '0')
  
  switch (format) {
    case 'date':
      return `${year}-${month}-${day}`
    case 'time':
      return `${hours}:${minutes}:${seconds}`
    case 'short':
      return `${month}-${day} ${hours}:${minutes}`
    case 'datetime':
    default:
      return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`
  }
}

/**
 * 
 * @param bytes 
 * @param decimals 1
 * @returns 
 */
export const formatFileSize = (bytes: number, decimals = 1): string => {
  if (bytes === 0) return '0 B'
  
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  
  return `${parseFloat((bytes / Math.pow(k, i)).toFixed(decimals))} ${sizes[i]}`
}

/**
 * 
 * @param str 
 * @param maxLength 
 * @param suffix '...'
 * @returns 
 */
export const truncateString = (
  str: string,
  maxLength: number,
  suffix = '...'
): string => {
  if (str.length <= maxLength) {
    return str
  }
  
  return str.slice(0, maxLength - suffix.length) + suffix
}

/**
 * 
 * @param pair 'BTC/USDT'
 * @returns basequote
 */
export const formatTradingPair = (pair: string) => {
  const [base, quote] = pair.split('/')
  return {
    base: base || '',
    quote: quote || '',
    display: pair,
    formatted: `${base}/${quote}`
  }
}

/**
 * 
 * @param value 
 * @param isPercent 
 * @returns 
 */
export const formatPriceChange = (value: number, isPercent = false) => {
  const isPositive = value >= 0
  const sign = isPositive ? '+' : ''
  const colorClass = isPositive ? 'text-green-600' : 'text-red-600'
  const bgColorClass = isPositive ? 'bg-green-100' : 'bg-red-100'
  
  const formatted = isPercent
    ? `${sign}${value.toFixed(2)}%`
    : `${sign}${formatCurrency(value)}`
  
  return {
    formatted,
    colorClass,
    bgColorClass,
    isPositive
  }
}