import axios, { AxiosInstance, AxiosResponse, InternalAxiosRequestConfig } from 'axios'
import { handleError } from '@/utils/errorHandler'

// axios
const createApiClient = (baseURL: string): AxiosInstance => {
  const client = axios.create({
    baseURL,
    timeout: 30000,
    headers: {
      'Content-Type': 'application/json',
    },
  })

  //  - token
  client.interceptors.request.use(
    (config: InternalAxiosRequestConfig) => {
      // localStoragetoken
      const authData = localStorage.getItem('auth-storage')
      if (authData) {
        try {
          const { state } = JSON.parse(authData)
          if (state?.token) {
            config.headers.Authorization = `Bearer ${state.token}`
          }
        } catch (error) {
          console.error('Failed to parse auth data:', error)
        }
      }
      return config
    },
    (error) => {
      return Promise.reject(error)
    }
  )

  //  - token
  client.interceptors.response.use(
    (response: AxiosResponse) => {
      return response
    },
    async (error) => {
      const originalRequest = error.config

      // 401 - token
      if (error.response?.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true

        try {
          // token
          const authData = localStorage.getItem('auth-storage')
          if (authData) {
            const { state } = JSON.parse(authData)
            if (state?.token) {
              // refresh token API
              const refreshResponse = await client.post('/auth/refresh', {
                token: state.token
              })
              
              const newToken = refreshResponse.data.token
              
              // localStoragetoken
              const updatedState = { ...state, token: newToken }
              localStorage.setItem('auth-storage', JSON.stringify({ state: updatedState }))
              
              // 
              originalRequest.headers.Authorization = `Bearer ${newToken}`
              
              // 
              return client(originalRequest)
            }
          }
        } catch (refreshError) {
          // 
          localStorage.removeItem('auth-storage')
          window.location.href = '/login'
          return Promise.reject(refreshError)
        }
      }

      // 
      const appError = handleError(error, 'api-client', false)
      
      // 
      return Promise.reject(appError)
    }
  )

  return client
}

// API
// API
const apiBaseUrl = import.meta.env.VITE_API_BASE_URL || 'http://localhost:3001/api/v1'
const tradingApiUrl = import.meta.env.VITE_TRADING_API_URL || 'http://localhost:8001/api/v1'

export const userServiceClient = createApiClient(apiBaseUrl)
export const tradingServiceClient = createApiClient(tradingApiUrl)

// API
export const handleApiResponse = <T>(response: AxiosResponse<T>): T => {
  return response.data
}

// API
export const handleApiError = (error: any): never => {
  // AppError
  if (error?.type && error?.message) {
    throw new Error(error.message)
  }
  
  // 
  const appError = handleError(error, 'api-error', true)
  throw new Error(appError.message)
}