import { create } from 'zustand'
import { persist } from 'zustand/middleware'
import toast from 'react-hot-toast'
import { aiApi } from '../services/api/ai'
import type { 
  ChatSession, 
  ChatMessage, 
  StrategyGenerationRequest, 
  GeneratedStrategy,
  MarketAnalysis 
} from '../services/api/ai'

interface AIState {
  // 
  chatSessions: ChatSession[]
  currentSession: ChatSession | null
  messages: ChatMessage[]
  isTyping: boolean
  
  // 
  generatedStrategies: GeneratedStrategy[]
  isGenerating: boolean
  currentGeneratedStrategy: GeneratedStrategy | null
  
  // 
  marketAnalyses: Record<string, MarketAnalysis> // key: symbol-timeframe
  isAnalyzing: boolean
  
  // 
  isLoading: boolean
  error: string | null
  
  // 
  createChatSession: (title?: string) => Promise<boolean>
  loadChatSessions: () => Promise<void>
  selectChatSession: (session: ChatSession | null) => void
  sendMessage: (message: string) => Promise<boolean>
  deleteChatSession: (sessionId: string) => Promise<boolean>
  clearCurrentMessages: () => void
  
  // 
  generateStrategy: (request: StrategyGenerationRequest) => Promise<GeneratedStrategy | null>
  optimizeStrategy: (code: string, objectives: string[]) => Promise<any>
  explainStrategy: (code: string) => Promise<any>
  clearGeneratedStrategies: () => void
  
  // 
  analyzeMarket: (symbol: string, exchange: string, timeframe: string) => Promise<MarketAnalysis | null>
  getTradingSignals: (symbol: string, exchange: string, timeframes: string[]) => Promise<any>
  assessRisk: (strategyCode?: string, portfolio?: any) => Promise<any>
  
  // 
  clearError: () => void
  reset: () => void
}

export const useAIStore = create<AIState>()(
  persist(
    (set, get) => ({
      // 
      chatSessions: [],
      currentSession: null,
      messages: [],
      isTyping: false,
      
      generatedStrategies: [],
      isGenerating: false,
      currentGeneratedStrategy: null,
      
      marketAnalyses: {},
      isAnalyzing: false,
      
      isLoading: false,
      error: null,

      // ===============  ===============
      
      // 
      createChatSession: async (title?: string) => {
        set({ isLoading: true, error: null })
        try {
          const session = await aiApi.createChatSession(title)
          set(state => ({
            chatSessions: [session, ...state.chatSessions],
            currentSession: session,
            messages: [],
            isLoading: false
          }))
          toast.success('')
          return true
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          toast.error(message)
          return false
        }
      },

      // 
      loadChatSessions: async () => {
        set({ isLoading: true, error: null })
        try {
          const sessions = await aiApi.getChatSessions()
          set({ 
            chatSessions: sessions, 
            isLoading: false 
          })
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          console.error('Load chat sessions error:', error)
        }
      },

      // 
      selectChatSession: (session: ChatSession | null) => {
        set({ 
          currentSession: session,
          messages: session?.messages || [],
          error: null
        })
      },

      // 
      sendMessage: async (message: string) => {
        const { currentSession } = get()
        if (!currentSession) {
          toast.error('')
          return false
        }

        // 
        const userMessage: ChatMessage = {
          role: 'user',
          content: message,
          timestamp: new Date().toISOString()
        }

        set(state => ({
          messages: [...state.messages, userMessage],
          isTyping: true,
          error: null
        }))

        try {
          // 
          const aiResponse = await aiApi.sendMessage(currentSession.id, message)
          
          // AI
          set(state => ({
            messages: [...state.messages, aiResponse],
            isTyping: false
          }))
          
          return true
        } catch (error: any) {
          const errorMessage = error.response?.data?.message || ''
          set({ 
            isTyping: false,
            error: errorMessage
          })
          toast.error(errorMessage)
          return false
        }
      },

      // 
      deleteChatSession: async (sessionId: string) => {
        try {
          await aiApi.deleteChatSession(sessionId)
          set(state => ({
            chatSessions: state.chatSessions.filter(s => s.id !== sessionId),
            currentSession: state.currentSession?.id === sessionId ? null : state.currentSession,
            messages: state.currentSession?.id === sessionId ? [] : state.messages
          }))
          toast.success('')
          return true
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          toast.error(message)
          return false
        }
      },

      // 
      clearCurrentMessages: () => {
        set({ messages: [], currentSession: null })
      },

      // ===============  ===============

      // 
      generateStrategy: async (request: StrategyGenerationRequest) => {
        set({ isGenerating: true, error: null })
        try {
          const strategy = await aiApi.generateStrategy(request)
          set(state => ({
            generatedStrategies: [strategy, ...state.generatedStrategies],
            currentGeneratedStrategy: strategy,
            isGenerating: false
          }))
          toast.success('')
          return strategy
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isGenerating: false })
          toast.error(message)
          return null
        }
      },

      // 
      optimizeStrategy: async (code: string, objectives: string[]) => {
        set({ isLoading: true, error: null })
        try {
          const result = await aiApi.optimizeStrategy(code, objectives)
          set({ isLoading: false })
          toast.success('')
          return result
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          toast.error(message)
          throw error
        }
      },

      // 
      explainStrategy: async (code: string) => {
        set({ isLoading: true, error: null })
        try {
          const result = await aiApi.explainStrategy(code)
          set({ isLoading: false })
          return result
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          toast.error(message)
          throw error
        }
      },

      // 
      clearGeneratedStrategies: () => {
        set({ 
          generatedStrategies: [], 
          currentGeneratedStrategy: null 
        })
      },

      // ===============  ===============

      // 
      analyzeMarket: async (symbol: string, exchange: string, timeframe: string) => {
        const key = `${symbol}-${timeframe}`
        set({ isAnalyzing: true, error: null })
        
        try {
          const analysis = await aiApi.analyzeMarket(symbol, exchange, timeframe)
          set(state => ({
            marketAnalyses: {
              ...state.marketAnalyses,
              [key]: analysis
            },
            isAnalyzing: false
          }))
          toast.success(`${symbol} `)
          return analysis
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isAnalyzing: false })
          toast.error(message)
          return null
        }
      },

      // 
      getTradingSignals: async (symbol: string, exchange: string, timeframes: string[]) => {
        set({ isLoading: true, error: null })
        try {
          const signals = await aiApi.getTradingSignals(symbol, exchange, timeframes)
          set({ isLoading: false })
          toast.success(`${symbol} `)
          return signals
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          toast.error(message)
          throw error
        }
      },

      // 
      assessRisk: async (strategyCode?: string, portfolio?: any) => {
        set({ isLoading: true, error: null })
        try {
          const assessment = await aiApi.assessRisk(strategyCode, portfolio)
          set({ isLoading: false })
          toast.success('')
          return assessment
        } catch (error: any) {
          const message = error.response?.data?.message || ''
          set({ error: message, isLoading: false })
          toast.error(message)
          throw error
        }
      },

      // ===============  ===============

      // 
      clearError: () => {
        set({ error: null })
      },

      // 
      reset: () => {
        set({
          chatSessions: [],
          currentSession: null,
          messages: [],
          isTyping: false,
          generatedStrategies: [],
          isGenerating: false,
          currentGeneratedStrategy: null,
          marketAnalyses: {},
          isAnalyzing: false,
          isLoading: false,
          error: null
        })
      }
    }),
    {
      name: 'ai-storage',
      // 
      partialize: (state) => ({
        chatSessions: state.chatSessions,
        generatedStrategies: state.generatedStrategies,
        marketAnalyses: state.marketAnalyses
      }),
    }
  )
)